<template>
  <div class="work-detail" v-if="work">
    <div class="work-hero">
      <img :src="coverUrl" alt="cover" />
    </div>
    <div class="work-content">
      <h1 class="work-title">{{ isEnglish ? work?.work_en?.title : work?.work_zh?.title }}</h1>
      <div class="work-meta">
        <span>{{ isEnglish ? 'Artist' : '藝術家' }}：{{ isEnglish ? work?.work_en?.artist : work?.work_zh?.artist }}</span>
      </div>
      <div class="work-description">{{ descriptionText }}</div>
    </div>
  </div>
  <div v-else class="work-detail loading">Loading...</div>
</template>

<script>
import { useRoute } from 'vue-router';
import { ref, onMounted, onBeforeUnmount } from 'vue';
import axios from 'axios';

export default {
  name: 'PortfolioDetil',
  setup() {
    const route = useRoute();
    const id = route.params.id;

    const isEnglish = ref(false);
    const work = ref(null);
    const coverUrl = ref('');
    const descriptionText = ref('');

    const resolveInitialLang = () => {
      // URL query 覆蓋 localStorage
      const q = route.query.lang;
      if (q === 'en') return true;
      if (q === 'zh') return false;
      try {
        const saved = localStorage.getItem('lang');
        return saved === 'en';
      } catch {
        return false;
      }
    };

    const pickFirstNonEmpty = (obj, keys) => {
      for (const key of keys) {
        const val = obj && typeof obj[key] === 'string' ? obj[key] : '';
        if (val && val.trim().length > 0) return val;
      }
      return '';
    };

    const stripHtml = (html) => {
      if (!html || typeof html !== 'string') return '';
      const withNewlines = html.replace(/<br\s*\/?>(\r\n)?/gi, '\n');
      const temp = document.createElement('div');
      temp.innerHTML = withNewlines;
      const text = temp.textContent || temp.innerText || '';
      return text;
    };

    const recalcDerived = () => {
      coverUrl.value = work.value ? `https://unzip-clab-api.clab.org.tw/${work.value.photo_1}` : '';
      const langBlock = isEnglish.value ? work.value?.work_en : work.value?.work_zh;
      const raw = pickFirstNonEmpty(langBlock || {}, ['description', 'proposal', 'note', 'summary', 'content', 'text']);
      descriptionText.value = stripHtml(raw);
    };

    const loadFromLocal = () => {
      try {
        const key = `work_${id}`;
        const raw = localStorage.getItem(key);
        if (!raw) return false;
        const parsed = JSON.parse(raw);
        work.value = parsed;
        recalcDerived();
        return true;
      } catch (e) {
        console.error('Failed to parse work data from localStorage:', e);
        return false;
      }
    };

    const fetchFromApi = async () => {
      try {
        const { data } = await axios.get(`https://unzip-clab-api.clab.org.tw/api/v1/works/${id}`);
        const entity = Array.isArray(data?.results) ? data.results[0] : (data || null);
        if (entity) {
          work.value = entity;
          recalcDerived();
        }
      } catch (e) {
        console.error('Failed to fetch work by id:', e);
      }
    };

    const handleStorage = (e) => {
      if (e.key === 'lang') {
        isEnglish.value = e.newValue === 'en';
        recalcDerived();
      }
    };

    onMounted(async () => {
      isEnglish.value = resolveInitialLang();
      const ok = loadFromLocal();
      if (!ok) await fetchFromApi();
      window.addEventListener('storage', handleStorage);
    });

    onBeforeUnmount(() => {
      window.removeEventListener('storage', handleStorage);
    });

    return { work, coverUrl, isEnglish, descriptionText };
  }
}
</script>

<style lang="scss" scoped>
.work-detail {
  background: #0e0e0e;
  color: #f2f2f2;
  min-height: 100vh;
}
.work-detail.loading {
  display: flex;
  align-items: center;
  justify-content: center;
}
.work-hero {
  width: 100%;
  height: 60vh;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
}
.work-hero img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.work-content {
  max-width: 1000px;
  margin: 0 auto;
  padding: 32px 20px 64px;
}
.work-title {
  font-size: 2rem;
  margin-bottom: 16px;
}
.work-meta {
  font-size: 0.95rem;
  color: #aaa;
  margin-bottom: 20px;
}
.work-description {
  line-height: 1.8;
  color: #ddd;
  white-space: pre-wrap;
}
</style>